export default {
	props: {
		direction: {
			type: String,
			default: "below"
		},
		width: {
			type: Number,
			default: 100
		},
		is_close: {
			type: Boolean,
			default: true
		},
		is_mask: {
			type: Boolean,
			default: true
		}
	},
	data() {
		return {
			open: false
		};
	},
	methods: {
		show() {
			this.open = true;
		},
		close() {
			this.open = false;
		}
	}
}
